<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">
                <div class="col-12">
                    <div class="card">
                        <div class="card-header">
                            <h3 class="card-title">Machine List</h3>
                            <div class="card-tools">
                                <router-link to="/machines/add" class="btn btn-sm btn-primary">
                                    <i class="fa fa-plus-square" aria-hidden="true"></i> Add New
                                </router-link>
                            </div>
                        </div>
                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0">
                            <table class="table table-hover">
                                <thead>
                                    <tr>
                                        <th>FAR No </th>
                                        <th>Name</th>
                                        <th>Owner</th>
                                        <th>Chart</th>
                                        <th>Purchase Date</th>
                                        <th>Machine Type</th>
                                        <th>Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="Machine in Machines.data" :key="Machine.id">

                                        <td class="align-middle">{{ Machine.far_no }}</td>
                                        <td class="align-middle">{{ Machine.name }}</td>
                                        <td class="align-middle">{{ Machine.owner }}</td>

                                        <td @click="view_pm_details(Machine)" class="align-middle">
                                            <VueGauge :refid="'mg-id' + Machine.id"
                                                :options="{ 'needleValue': Machine.next_pm_date.early, 'arcDelimiters': [10, 36, 78], arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '80' }" />

                                        </td>
                                        <td class="align-middle">{{ Machine.purchase_date }}</td>
                                        <td class="align-middle">{{ Machine.machine_type }}</td>
                                        <!-- <td><img v-bind:src="'/' + Machine.photo" width="100" alt="Machine"></td> -->
                                        <td class="align-middle">
                                            <router-link :to="'/machines/edit/' + Machine.id" class="text-green"> <i
                                                    class="fa fa-edit blue"></i> </router-link>
                                            /
                                            <a href="javascript:void(0);" @click="deleteMachine(Machine.id)">
                                                <i class="fa fa-trash red"></i>
                                            </a>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer">
                            <pagination :data="Machines" @pagination-change-page="getResults" v-if="Machines">
                            </pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>


        </div>

        <!-- Modal -->
        <div class="modal fade" id="pmModal" tabindex="-1" role="dialog" aria-labelledby="pmModal" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">View PM Details</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <div class="modal-body">


                        <div class="container">
                            <div class="row">
                                <div class="col-sm" v-if="pmModalData.is_maintenance_date">
                                    <VueGauge refid="popg1"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.maintenance_date, 'arcDelimiters': [10, 36, 78], arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '150' }"
                                         />
                                        <p>Maintenance Date</p>
                                </div>
                                <div class="col-sm" v-if="pmModalData.is_consumtion_km_run">
                                    <VueGauge refid="popg2"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.km_run, 'arcDelimiters': [10, 36, 78], arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '150' }"
                                         />
                                        <p>KM Run</p>
                                </div>
                                <div class="col-sm" v-if="pmModalData.is_consumtion_hr_run" >
                                    <VueGauge refid="popg3"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.hr_run, 'arcDelimiters': [10, 36, 78], arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '150' }"
                                        />
                                        <p>Hr Run</p>
                                </div>
                                <div class="col-sm" v-if="pmModalData.is_production_fig">
                                    <VueGauge refid="popg4"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.production_fig, 'arcDelimiters': [10, 36, 78], arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '150' }"
                                         />
                                        <p>Production Fig</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>


    </section>
</template>

<script>

import VueGauge from 'vue-gauge';
export default {
    name: "Machine",
    components: {
        VueGauge,
    },
    data() {
        return {
            Machines: {},
            pmModalData: ''
        }
    },
    methods: {
        async view_pm_details(data) {
            this.pmModalData = data
            $('#pmModal').modal('show');
        },

        getResults(page = 1) {

            axios.get('api/machine?page=' + page).then(({ data }) => (this.Machines = data.data));


        },
        loadMachines() {
            // if(this.$gate.isAdmin()){
            axios.get("api/machine").then(({ data }) => (this.Machines = data.data));
            // }
        },

        deleteMachine(id) {
            Swal.fire({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) => {
                // Send request to the server
                if (result.value) {
                    this.form.delete('api/machine/' + id).then(() => {
                        Swal.fire(
                            'Deleted!',
                            'Your file has been deleted.',
                            'success'
                        );
                        // Fire.$emit('AfterCreate');
                        this.loadMachines();
                    }).catch((data) => {
                        Swal.fire("Failed!", data.message, "warning");
                    });
                }
            })
        },

    },
    mounted() {

    },
    created() {

        this.loadMachines();
    },

}
</script>
